(function() {
    for (var k in webix.skin)
    {
        if (typeof(webix.skin[k]) != "function") {
            webix.skin[k] = {
                topLayout:"wide",
                //bar in accordion
                barHeight:32,            //!!!Set the same in skin.less!!!
                tabbarHeight: 32,
                rowHeight:28,
                toolbarHeight:28,
                listItemHeight:32,        //list, grouplist, dataview, etc. 
                inputHeight:32,
                inputPadding: 2,
                menuHeight: 28,
                menuMargin:0,
                labelTopHeight: 16,

                inputSpacing: 4,
                borderWidth: 0,

                sliderHandleWidth: 16,
                sliderPadding: 10,
                sliderBorder: 1,

                //margin - distance between cells
                layoutMargin:{ space:5, wide:4, clean:0, head:4, line:-1, toolbar:4, form:8  },
                //padding - distance insede cell between cell border and cell content
                layoutPadding:{ space:5, wide:0, clean:0, head:0, line:0, toolbar:4, form:8  },
                //space between tabs in tabbar
                tabMargin:4,

                popupPadding: 8,

                calendarHeight: 70,
                padding:3,

                optionHeight: 23
            };
        }
    }

    webix.skin.set('flat');
})();